import { Component } from '@angular/core';
import { AlertController, IonicPage } from 'ionic-angular';

import { Subject } from 'rxjs/Subject';
import { LoginServiceProvider } from '../../providers/login-service/login-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SqliteServiceProvider } from '../../providers/sqlite-service/sqlite-service';



/**
 * Generated class for the LoginPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  private ngUnsubscribe: Subject<void> = new Subject<void>();
  registerCredentials = { username: '', password: '' };
  allowed: boolean = false;
  connDetails;
  //dataLoaded: boolean = false;
  rootPage: any;
  error;

  constructor(
    public storage: SqliteServiceProvider, private loginService: LoginServiceProvider, private alertCtrl: AlertController,
    private sharedService: SharedServiceProvider, public splashScreen: SplashScreen
  ) {
    //console.log("LoginPage loading...");

  }

  public login() {

    this.loginService.login(this.registerCredentials)
      .takeUntil(this.ngUnsubscribe)
      .subscribe(
        (allowed) => {
          this.allowed = allowed;
        },
        error => {
          this.showError(error);
          // this.spinnerService.hideSpinner();
        },
        () => {


          if (this.allowed) {
            //this.spinnerService.showSpinner();
            // Retrieve current login details
            var loginDetails = JSON.parse(localStorage.getItem('login'));
            // If no login details assume new installation or reset
            if (!loginDetails) {
              // 1st time ever logged in on PDA
              loginDetails = {
                username: this.registerCredentials.username,
                time: new Date().toISOString()
              };

            }
            else {
              // update last login time
              loginDetails.time = new Date().toISOString();

            }

            // Store details in local storage & sqllite  
            var uid = this.registerCredentials.username;
            (async () => {
              if (!this.sharedService.desktopTesting) {
                await this.storage.set('login', JSON.stringify(loginDetails)).then((res) => {
                  if (res)
                    console.log("login is set");
                  else
                    console.log("login is not set");
                });

                await this.storage.set('userId', uid).then((res) => {
                  if (res)
                    console.log("userid is set");
                  else
                    console.log("userid is not set");
                });
              }

              //this.storage.printAll();

              localStorage.setItem('login', JSON.stringify(loginDetails));
              localStorage.setItem('userId', uid);

              /* await this.storage.get('userId').then((val) => {
                alert('GET:userId set is:' + val);
                console.log('GET:userId set is:' + val);
              }); */
              //this.splashScreen.show();

              //Redirect to the root app component.
              document.location.href = 'index.html';
            })();
          } else {
            this.showError("Access Denied!");
            //this.spinnerService.hideSpinner();
          }
        });//login subscription ends here  

  }//login() ends here

  showError(text) {
    let alert = this.alertCtrl.create({
      title: 'Login failed!',
      subTitle: text,
      buttons: ['OK'],
      enableBackdropDismiss: false
    });
    alert.present();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}



